}

.App-header {
  background-color: #f4a4f0;
  min-height: 10vh;
  /*display: flex;*/
  margin: 10px 1px 10px 10px;
  justify-content:space-between ;
  font-size: calc(8px + 2vmin);
  color: white;

}

.App-link {
  color: #000000; 
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

